import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from 'modules/core/dashboard/dashboard.component';
import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';

const routes: Routes = [
  {
    path: 'dashboard',
    canActivate: [AuthenticationGuard, AuthorizationGuard],
    component: DashboardComponent,
    data: {
      title: 'Dashboard'
    }
  },
  {
    path: 'user',
    loadChildren: './user/user.module#UserModule'
  },
  {
    path: 'employee',
    loadChildren: './employee/employee.module#EmployeeModule'
  },
  {
    path: 'organization',
    loadChildren: './organization/organization.module#OrganizationModule'
  },
  {
    path: 'leave',
    loadChildren: './leave/leave.module#LeaveModule'
  },
  {
    path: 'comp-off',
    loadChildren: './comp-off/comp-off.module#CompensatoryOffModule'
  },
  {
    path: 'work-from-home',
    loadChildren: './work-from-home/work-from-home.module#WorkFromHomeModule'
  },
  {
    path: 'access-control',
    loadChildren: './access-control/access-control.module#AccessControlModule'
  },
  {
    path: 'support',
    loadChildren: './support/support.module#SupportModule'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ApplicationRoutingModule { }
