import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { GraphqlService } from 'core/services/graphql.service';
import { DepartmentSummary } from 'shared/widgets/department-summary/department-summary.component';
import { LeaveSummary } from 'shared/widgets/leave-summary/leave-summary.component';
import { UpcomingHoliday } from 'shared/widgets/upcoming-holiday/upcoming-holiday.component';
import { DASHBOARD } from './dashboard.graphql';
import { ExaTask } from '../../../core/providers/exatask.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: 'dashboard.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [GraphqlService]
})
export class DashboardComponent implements OnInit {

  public department_summary: DepartmentSummary = new DepartmentSummary();
  public leave_summary: LeaveSummary[] = [];
  public upcoming_holiday: UpcomingHoliday = new UpcomingHoliday();

  constructor(
    private graphqlService: GraphqlService,
    public exatask: ExaTask
  ) {
    this.exatask.setTitle('Dashboard');
  }

  ngOnInit() {

    const self = this;
    this.graphqlService.query(DASHBOARD.loc.source.body).then((response) => {

      self.department_summary = response.data.department_summary;
      self.leave_summary = response.data.leave_summary;
      self.upcoming_holiday = response.data.upcoming_holiday;
    });
  }
}
