import { Injectable } from '@angular/core';
import { Api } from './api.service';

@Injectable()
export class GraphqlService extends Api {

  private _base_url: string = '/graphql';

  query(query: any) {

    query = query.replace(/\n+/g, ' ');
    query = query.replace(/ +/g, ' ');

    const gql = new Map<string, string>();
    gql.set('query', query);

    return this.get(this._base_url, gql).then((response) => response);
  }
}
